@import '../../global.scss';

.intro{
    background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }

    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 580px;
            height: 700px;
            display: flex;
            align-items: center;
            justify-content: center;
            float: left;

            @include mobile{
                align-items: flex-start;
            }

            img{
                height: 70%;
                border-radius: 20%;

                @include mobile{
                    height: 50%;
                }
            }
        }
    }

    .right{
        flex: 0.5;
        position: relative;
        background-color: white;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left: 0;
                align-items: center;
              }

            h1{
                font-size: 60px;
                margin: 10px 0;

                @include mobile{
                    font-size: 40px;
                  }
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;

                @include mobile{
                    font-size: 20px;
                  }
            }

            a{
                position: absolute;
                bottom: 10px;
                left: 50%;
                @include mobile{
                    left: 45%;
                  }

                .arrow{
                    font-size: 50px;
                    animation: arrowBlink 2s infinite;
                }

                @keyframes arrowBlink {
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
    }
}